.subtitle {
	font-size: 17px;
	line-height: 21px;
	font-weight: 400;
	color: #222222;
	text-align: center; }
.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/backgarund/backgraund2.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		padding-top: 92px;
		&_1 {
			background: url('../img/cards/card1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/cards/card2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/cards/card3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/cards/card4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/cards/card5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/cards/card6.png') center (center / cover) no-repeat; }
		margin: 0 auto;
		margin-bottom: 19px;

		&_subtitle {
			font-size: 24px;
			font-weight: 400;
			font-style: normal;
			letter-spacing: normal;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase;
			color: #ffffff; }

		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 219px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			margin: 0 auto;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main_color;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				height: 14px;
				width: 2px;
				background-color: $main_color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
						background-color: $second_color; } } } } }

.car {
	height: 900px;
	background: url(../img/backgarund/backgarund_car.png) center center/cover no-repeat;
	padding-top: 65px;
	&_title {
		margin-top: 32px;
		color: #ffffff;
		font-size: 38px;
		font-weight: 700;
		font-style: normal;
		letter-spacing: normal;
		line-height: 48px;
		text-align: center; }
	&_label {
		width: 112px;
		height: 24px;
		background-color: #ffffff;
