		font-size: 11px;
		font-weight: 400;
		text-align: center;
		text-transform: uppercase;
		line-height: 26px;
		margin: 0 auto; }
	&_subtitle {
		color: #ffffff;
		font-size: 17px;
		font-weight: 100;
		font-style: normal;
		letter-spacing: normal;
		line-height: 21px;
		text-align: center; }
	&_block {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 56px auto;
		.car_logo {
			position: absolute;
			top: 25px;
			transform: translateX(50%);
			right: 50%; } }
	&_text {
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		font-style: normal;
		letter-spacing: normal;
		line-height: 24px;
		text-align: center;
		margin-top: 576px; } }

.reguire {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		font-size: 22px;
		font-weight: 400;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			margin-bottom: 10px;
			line-height: 24px;
			font-size: 17px;
			font-weight: 300;
			color: #222222;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background-image: url(../img/icons/icon_blue.png); } } }
	&_descr {
		font-size: 17px;
		font-weight: 300;
		color: #1ca8c3; }
	.warning {
		.reguire_title {
			color: $second_color; }
		.reguire_list {
			li {
				&:before {
					background-image: url(../img/icons/icon_red.png); } } } } }
